* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  background-color: rgb(30, 15, 188);
  display: flex;
  justify-content: center;
}

input {
  width: 200px;
  margin-right: 20px;
}

button {
  outline: none;
}
.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.load-more.is-hidden {
  display: none;
}

.gallery {
  display: flex;
  align-items: center;
  margin-top: 50px;
  flex-wrap: wrap;
}

.photo-card {
  width: 355px;
  margin-right: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #eeeeee;
}

.photo-card:nth-child(4n) {
  margin-right: 0px;
}

.photo-card:nth-last-child(-n + 4) {
  margin-bottom: 0;
}

.info {
  align-items: center;
  display: flex;
  justify-content: center;
  justify-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 14px;
  padding-right: 14px;
  border-left: 1px solid #eeeeee;
  border-right: 1px solid #eeeeee;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-items: center;
  width: 322px;
  margin-bottom: 4px;
  margin-right: 5px;
  margin-left: 5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 2;
  letter-spacing: 0.06em;
}